import React, { Component } from 'react';
import { Button, ModalHeader, Modal, ModalBody, Label, Row, Col } from 'reactstrap';
import { Control, Form, Errors } from 'react-redux-form';

import Register from './RegisterComponent';

const required = (val) => val && val.length;

class Login extends Component {
    constructor(props) {
        super(props);

        this.state = {
            modal: false
        };

        this.toggle = this.toggle.bind(this);
        this.onSubmit = this.onSubmit.bind(this);
    }

    onSubmit(values) {
        this.props.fetchUserInfo(values.username, values.password);
    }

    toggle() {
        this.setState({
            modal: !this.state.modal
        });
    }

    render() {
        return (
            <div>
                <Button style={{ background: 'transparent' }} onClick={this.toggle}>
                    <span className="fa fa-sign-in" /> Login
                </Button>
                <Modal isOpen={this.state.modal} toggle={this.toggle} className={this.props.className}>
                    <ModalHeader toggle={this.toggle}>Login</ModalHeader>
                    <ModalBody>
                        <Form model="login" onSubmit={(values) => this.onSubmit(values)}>
                            <Row className="form-group">
                                <Label htmlFor=".username" md={2}>
                                    Username
                                </Label>
                                <Col md={10}>
                                    <Control.text
                                        model=".username"
                                        id="username"
                                        name="username"
                                        placeholder="Username"
                                        className="form-control"
                                        validators={{
                                            required
                                        }}
                                    />
                                    <Errors
                                        className="text-danger"
                                        model=".username"
                                        show="touched"
                                        messages={{
                                            required: 'Required',
                                            error: 'Username doesn\'t exist!'
                                        }}
                                    />
                                </Col>
                            </Row>
                            <Row className="form-group">
                                <Label htmlFor=".password" md={2}>
                                    Last Name
                                </Label>
                                <Col md={10}>
                                    <Control.password
                                        model=".password"
                                        id="password"
                                        name="password"
                                        placeholder="Password"
                                        className="form-control"
                                        validators={{
                                            required
                                        }}
                                    />
                                    <Errors
                                        className="text-danger"
                                        model=".password"
                                        show="touched"
                                        messages={{
                                            required: 'Required'
                                        }}
                                    />
                                </Col>
                            </Row>
                            <Row>
                                <Col xl={{ size: 3, offset: 1 }}>
                                    <Register />
                                </Col>
                                <Col xl={{ size: 3, offset: 4 }}>
                                    <Button
                                        block
                                        className="item-button"
                                        value="submit"
                                        color="primary"
                                    >
                                        Login
                                    </Button>
                                </Col>
                            </Row>
                        </Form>
                    </ModalBody>
                </Modal>
            </div>
        );
    }
}

export default Login;
